export type TagMap = {
  [key: string]: MyCSS3DObject[]
}

export type pointConfig = {
  color?: number,
  size?: number
}

export interface MyCSS3DObject extends CSS3DObject {
  name: string
}

export type TagOptions = {
  position: MyPostion,
  scale?: number,
  tagName?: string
}